import React, { useState, useEffect } from 'react';
import { RouteComponentProps, useHistory } from 'react-router-dom';
import { CDataTable, CButton } from '@coreui/react';
import { useTranslation } from 'react-i18next';
import ModalCreateCategory from './ModalCreateCategory';
import { ConfirmPopup, ConfirmPopupPassword, DatePicker } from '../../../components';
import Search from '../../../components/search/Search';
import { SUCCESS } from '../../../store/callApi/actions';
import config from '../../../config';
import { adminDeleteContent, pagination, renderFields } from '../../../extensions';
import { useApi } from 'store/callApi';

const initialValuesCreate = {
  title: '',
  description: '',
};

type CategoryDataType = {
  no: number;
  id: string;
  title: string;
  description: string;
  created_date: number;
  total_video: number;
};

type CategoriesDataType = {
  data: CategoryDataType[];
  total: number;
  totalPages: number;
  page: number;
  loading: boolean;
};

const pageSize = 10;

const Categories: React.FC<RouteComponentProps> = (props) => {
  const { t } = useTranslation();
  const { callApi } = useApi();

  const history = useHistory();

  const [categories, setCategories] = useState<CategoriesDataType>({
    data: [],
    total: 0,
    totalPages: 0,
    page: 1,
    loading: true,
  });

  const [createCategoryData, setCreateCategoryData] = useState({ show: false, initialValues: initialValuesCreate });
  const [popupDeleteCategory, setPopupDeleteCategory] = useState({ show: false, id: '', name: '' });
  const [popupDeleteCategory2, setPopupDeleteCategory2] = useState({ show: false, id: '', name: '' });
  const [fromDate, setFromDate] = useState<any>(null);
  const [toDate, setToDate] = useState<any>(null);
  const [searchName, setSearchName] = useState('');

  const fields = [
    { key: 'no', _style: { width: '5%' } },
    { key: 'title', label: 'Category name', _style: { width: '15%' } },
    { key: 'description', _style: { width: '20%' } },
    { key: 'created_date', _style: { width: '10%' }, isDate: true },
    { key: 'total_video', label: 'Number Of Videos', _style: { width: '10%' } },
    {
      key: 'action',
      _style: { width: '8%' },
      renderContent: (item: any) => (
        <>
          <CButton
            color="primary"
            variant="outline"
            shape="square"
            size="sm"
            style={{
              marginRight: 10,
            }}
            onClick={() => setPopupDeleteCategory({ show: true, id: item.id, name: item.title })}
          >
            <i className="fas fa-trash-alt"> </i>
          </CButton>
          <CButton
            color="primary"
            variant="outline"
            shape="square"
            size="sm"
            onClick={() => history.push(`/admin/mobility/categories/${item.id}`)}
          >
            <i className="fas fa-pencil-alt"> </i>
          </CButton>
        </>
      ),
    },
  ];

  const pageChange = (newPage: number) => {
    if (categories.page !== newPage) {
      getCategories(newPage, searchName);
    }
  };

  useEffect(() => {
    getCategories(1, searchName);
  }, [fromDate, toDate]);

  let noItems = <div />;

  if (!categories.loading) {
    if (!categories.total) noItems = <div>{t('no-items-found')}</div>;
  }

  const getCategories = (newPage: number, searchName: string) => {
    setCategories({ ...categories, loading: true });
    callApi(
      {
        method: 'get',
        api: config.rest.adminGetMobilityCategories(
          newPage,
          searchName,
          fromDate ? fromDate.getTime() / 1000 : null,
          toDate ? toDate.getTime() / 1000 + 86399 : null,
        ),
      },
      (response: any) => {
        const { status, data } = response;
        if (status === SUCCESS) {
          setCategories({
            data: data.data.map((item: any, index: number) => {
              return { ...item, no: index + 1 + pageSize * (newPage - 1) };
            }),
            total: data.total,
            totalPages: Math.ceil(data.total / pageSize),
            page: newPage,
            loading: false,
          });
        } else {
          setCategories((categories: any) => {
            return { ...categories, loading: false };
          });
        }
      },
    );
  };

  const handleDeleteCategory = (id: string, password: string) => {
    setPopupDeleteCategory({ ...popupDeleteCategory, show: false });
    setCategories({ ...categories, loading: true });
    callApi({ method: 'post', api: config.rest.confirmPassword(), body: { password } }, (response: any) => {
      const { status } = response;
      if (status === SUCCESS) {
        callApi({ method: 'delete', api: config.rest.adminDeleteMobilityCategory(id) }, (response: any) => {
          const { status } = response;
          if (status === SUCCESS) {
            getCategories(categories.page, searchName);
          } else
            setCategories((categories: any) => {
              return { ...categories, loading: false };
            });
        });
      } else
        setCategories((categories: any) => {
          return { ...categories, loading: false };
        });
    });
  };

  return (
    <div className="px-5 mx-3" style={{ marginTop: 10 }}>
      <ModalCreateCategory
        callApi={callApi}
        initialValues={createCategoryData.initialValues}
        show={createCategoryData.show}
        handleClose={() => setCreateCategoryData({ ...createCategoryData, show: false })}
        searchCategories={() => getCategories(categories.page, searchName)}
      />
      <ConfirmPopup
        isVisible={popupDeleteCategory.show}
        title="Delete Category"
        renderContent={adminDeleteContent(
          'Are you sure to delete the category',
          popupDeleteCategory.name,
          'If Yes, all videos in this category are also deleted immediately.',
        )}
        leftButtonText="No"
        rightButtonText="Yes"
        leftButtonOnPress={() => setPopupDeleteCategory({ ...popupDeleteCategory, show: false })}
        rightButtonOnPress={() => {
          setPopupDeleteCategory2({ show: true, id: popupDeleteCategory.id, name: popupDeleteCategory.name });
          setPopupDeleteCategory({ ...popupDeleteCategory, show: false });
        }}
      />
      <ConfirmPopupPassword
        isVisible={popupDeleteCategory2.show}
        title="Delete Category"
        leftButtonText="Cancel"
        rightButtonText="Delete"
        type="category"
        name={popupDeleteCategory2.name}
        callback={(password: string) => handleDeleteCategory(popupDeleteCategory2.id, password)}
        handleClose={() => setPopupDeleteCategory2({ ...popupDeleteCategory2, show: false })}
      />
      <div>
        <h2>Category Management</h2>
        <div className="d-flex align-items-center mt-4" style={{ marginBottom: '2.4rem' }}>
          <Search
            className="mr-5"
            searchName={searchName}
            setSearchName={setSearchName}
            searchEmpty={true}
            callbackSearch={(searchName: string) => getCategories(1, searchName)}
          />
          <div>From date:</div>
          <div>
            <DatePicker maxDate={toDate || new Date()} date={fromDate} setDate={setFromDate} />
          </div>
          <div>To date:</div>
          <div>
            <DatePicker minDate={fromDate} date={toDate} setDate={setToDate} />
          </div>
          <div style={{ textAlign: 'right', flex: 1 }}>
            <CButton
              color="primary"
              onClick={() =>
                setCreateCategoryData({
                  show: true,
                  initialValues: {
                    title: '',
                    description: '',
                  },
                })
              }
            >
              Add new category
            </CButton>
          </div>
        </div>
      </div>
      <div className="hb-table">
        <CDataTable
          items={categories.data}
          fields={fields}
          loading={categories.loading}
          noItemsViewSlot={noItems}
          hover
          striped
          scopedSlots={renderFields(fields)}
        />
      </div>
      {pagination(categories.totalPages, categories.page, pageChange, categories.total)}
    </div>
  );
};

export default Categories;
